    border: 5px solid #7d7d7d;
  }
  
  #project-right {
    height: 100%;
    width: 100%;
    text-align: left;
    margin: auto;
    padding-left: 20px;
  }
  
  .project-btns{
    padding-top: 20px; 
    display: flex;
  }
  
  .project-top .project-btns {
    position: absolute;
    display: none;
  }
  
  .project-btns .btn {
    text-transform: uppercase;
    border: 1.5px solid white;
    color: white;
    font-size: 16px;   
    padding: 10px; 
    width: -webkit-fit-content; 
    width: -moz-fit-content; 
    width: fit-content;
    align-items: center;
    margin-right:10px;
  }
    
  .project-top:hover .project-btns{
    display: block;
  }
  .project-top:hover img {
    opacity: 0.5;
    transition: opacity .25s ease-in-out;
  }
  
  .project-top img {
    display: block;
      width: 100%;
      max-width: 500px; /*actual image width*/
      height: auto; /* maintain aspect ratio*/
      margin: auto;
  }
  
  .image {
      max-width: 550px;
